 .promo {
     padding: 160px 0px;
     color: #fff;
     display: flex;
     align-items: center;

     @media only screen and (max-width: 768px) {
         flex-direction: column;
         padding: 50px 0;
     }

     &__action {
         margin-top: 40px;
         position: relative;
     }

     &__title {
         margin-bottom: 40px;
         font-size: 60px;

         @media only screen and (max-width: 768px) {
             font-size: 30px;
             margin-left: 15px;
         }
     }

     &__image {
         position: relative;
         background-size: 100%;
         margin-right: 240px;

         @media only screen and (max-width: 768px) {
             margin-right: 0;
             margin-top: 50px;
         }

         img {
             max-width: 100%;
         }
     }

     &__desc {
         font-size: 18px;

         @media only screen and (max-width: 768px) {
             font-size: 16px;
             margin-left: 20px;
         }
     }
 }

 